import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Mais de 938 mil km² são constituídos em Unidades de Conservação Ambiental em 2018</h2>

        <p className="simple-text">Instituídas para fortalecer a proteção de áreas de preservação, as unidades de conservação ambiental criadas pelo Governo do Brasil em 2018 representam o equivalente a quase duas vezes o território da França. Agora, o País passa a ter 333 reservas em todo o território nacional.</p>
        <p className="simple-text">Em abril, foram decretadas três reservas extrativistas: Arapiranga Tromaí, Baía do Tubarão e Itapetiningano, todas no Maranhão. São mais de 400 mil hectares preservados que vão beneficiar 13 mil famílias que vivem nesses locais de pesca artesanal. Além das reservas, um parque nacional e uma área de proteção ambiental em áreas de caatinga também entraram para a lista de zonas preservadas.</p>
        <p className="simple-text">Em março, foram outras quatro unidades marinhas delimitadas, as maiores da história. Os arquipélagos São Pedro e São Paulo, em Pernambuco, e o de Trindade e Martim Vaz, em Vitória (ES), somam mais de 925 mil quilômetros quadrados em áreas que serão em parte para uso sustentável e em outra de proteção integral. Com isso, o Brasil passará de 1,5% de áreas marinhas protegidas para 25%. </p>
        <p className="simple-text">Destaque ainda para o decreto que triplica a área do Parque da Chapada dos Veadeiros, em Goiás, e a ampliação de outras duas unidades de conservação: Estação Ecológica do Taim, no Rio Grande do Sul, e a Reserva Biológica União, no Rio de Janeiro.</p>
        <p className="smaller-title-text">Fonte: ICMBio e Ministério do Meio Ambiente</p>
    </div>
)


