import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Com lei de governança e melhor gestão, estatais fecham 2017 com lucro de R$ 28,4 bilhões </h2>

        <p className="simple-text">As empresas estatais federais evoluíram de um prejuízo de R$ 32 bilhões em 2015 para um lucro de R$ 28,4 bilhões em 2017, representando uma variação positiva de R$ 60,4 bilhões. O desempenho é resultado direto de investimento em gestão e da Lei da Responsabilidade das Estatais, sancionada pelo presidente da República, Michel Temer, em 2016.</p>
        <p className="eye-text">Com a sanção da lei, transparência, regras mais rígidas para compras, licitações e nomeações e boas práticas de governança corporativas são requisitos exigidos para a gestão das estatais. Em 2016, os primeiros resultados apareceram: saldo positivo de R$ 9 bilhões. </p>
        <p className="simple-text">Esse desempenho expressivo é o acumulado apenas das maiores empresas: Banco do Brasil, Banco Nacional de Desenvolvimento Econômico e Social (BNDES), Caixa Econômica Federal, Eletrobras e Petrobras.</p>
        <p className="smaller-title-text">Fonte: Ministério do Planejamento, Portal Legislação</p>
    </div>
)


