import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Nova grade curricular unifica ensino público e privado no Brasil</h2>
        <p className="simple-text">Pela primeira vez na história, o Brasil possui uma Base Nacional Comum Curricular (BNCC), determinando o que deve ser ensinado nas escolas públicas e privadas, unificando o ensino em todo o território nacional. Com a mudança, o País se alinha aos melhores sistemas educacionais do mundo.</p>
        <p className="eye-text">A etapa da base que diz respeito à educação infantil e ao ensino fundamental foi homologada em 2017 e já é uma norma para as redes e escolas de todo o País. Já a parte do ensino médio deve ser homologada até o fim de 2018, após debate entre governo e sociedade.</p>
        <p className="simple-text">Visando ao aprimoramento do sistema, o Governo do Brasil também impulsiona o ensino em tempo integral. Até 2020, serão investidos, via Política de Fomento à Escola em Tempo Integral, R$ 1,5 bilhão na implantação do modelo.</p>
        <p className="smaller-title-text">Fonte: Ministério da Educação</p>
    </div>
)


