import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Governo investe em teste rápido para diagnóstico de zika e garante assistência a gestantes e crianças</h2>
        <p className="simple-text">Para dar mais agilidade ao diagnóstico e tranquilizar gestantes e crianças, o Governo do Brasil investiu R$ 119 milhões na compra de 3,5 milhões de exames com capacidade de detectar o vírus zika em 20 minutos. A medida possibilitou um controle eficaz da contaminação e a assistência imediata a crianças e gestantes.</p>
        <p className="eye-text">Outros dois milhões de testes rápidos para dengue e um milhão para chikungunya também foram adquiridos pelo governo para abastecer estados e municípios. No três casos, o material permite obter o resultado de maneira eficaz, mesmo que a contaminação tenha ocorrido anteriormente. Todos os insumos entraram na tabela do Sistema Único de Saúde (SUS).</p>
        <p className="simple-text">Com medidas de prevenção e de mobilização contra o mosquito Aedes aegypti, transmissor da dengue, zika e chikungunya, foi possível reduzir drasticamente a incidência das três doenças. Em 2018, houve redução de 92% nos casos de zika, 83% nos de dengue e 32% nos de chikungunya em relação a 2017, ano que já apresentava números positivos em relação ao anterior.</p>
        <p className="smaller-title-text">Fonte: Ministério da Saúde</p>
    </div>
)


