import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Plano Agro+ moderniza e reduz a burocracia do agronegócio brasileiro</h2>
        <p className="simple-text">Criado pelo Governo do Brasil em 2017, o Plano Agro+ já simplificou normas e modernizou operações em 830 ações. O intuito das mudanças é melhorar o fluxo de informações, reduzir a burocracia, simplificar as normas e tornar mais eficientes as operações do agronegócio brasileiro.</p>
        <p className="eye-text">Um dos objetivos do plano é aumentar de 7% para 10% a participação do Brasil no comércio agrícola mundial. A ação corrobora com o saldo positivo da balança comercial brasileira, gerando mais riqueza para todo o País.</p>
        <p className="simple-text">Ponto de destaque no plano, a atualização de normas e procedimentos era uma reivindicação antiga do setor produtivo, que contribuiu com sugestões na execução do programa. A estimativa é que, com o Agro+, o setor privado e o governo tenham ganho de eficiência da ordem de R$ 1 bilhão ao ano, o que representa 0,2% do faturamento anual do agronegócio.</p>
        <p className="smaller-title-text">Fonte: Ministério da Agriculta, Pecuária e Abastecimento  </p>
    </div>
)


