import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Após quedas consecutivas, indústria automotiva cresce 17,2% em 2017</h2>
        <p className="simple-text">Após quatro anos de queda, a produção de automóveis, reboques e carrocerias no Brasil teve um aumento de 17,2% em 2017. Com melhora no ambiente econômico, a confiança das empresas também cresceu em relação à economia brasileira, possibilitando a retomada da produção.</p>
        <p className="eye-text">O setor automotivo é responsável por 22% do PIB industrial brasileiro e emprega direta e indiretamente cerca de 1,6 milhão de trabalhadores.</p>
        <p className="simple-text">Para 2018, o cenário continua animador. Nos primeiros três meses do ano, foram fabricados 699,7 mil veículos no Brasil, um aumento de 14,6% na comparação com o mesmo período de 2017. Segundo a Associação Nacional dos Fabricantes de Veículos Automotores (Anfavea), a previsão é que a produção cresça 13,2% na comparação com o ano passado, atingindo a marca de 3,055 milhões de veículos montados.</p>
        <p className="smaller-title-text">Fonte: IBGE e Anfavea</p>
    </div>
)


