import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Governo habilita 206 UPAs 24 horas, ampliando o atendimento à populaçã</h2>
        <p className="simple-text">Em dois anos, 206 unidades de pronto atendimento (UPAs) foram habilitadas e qualificadas pelo Governo do Brasil, garantindo o atendimento de mais de 20 milhões de brasileiros. Todas as solicitações de custeio foram atendidas, permitindo que estados e municípios tenham suporte financeiro da União.</p>
        <p className="eye-text">Atualmente, 572 UPAs 24h atendem os brasileiros em todo o território nacional. Outras 218 estão em obras, 10 em ação preparatória e outras 155 se encontram concluídas, em fase de entrada em funcionamento.</p>
        <p className="simple-text">Ainda em 2007, o governo também realizou mudanças no modelo assistencial e de funcionamento das estruturas. Passando de três para oito modalidades de habilitação, a gestão ficou mais flexível, evitando que unidades prontas para funcionamento continuassem fechadas por motivos administrativos.</p>
        <p className="smaller-title-text">Fonte: Ministério da Saúde</p>
    </div>
)


