import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Cartórios de todo o Brasil poderão emitir passaporte e carteiras de identidade</h2>
        <p className="simple-text">Obter a carteira de identidade e o passaporte fica mais fácil para os brasileiros em 2018. Com medida publicada pela Corregedoria Nacional de Justiça, a emissão de tais documentos não está mais condicionada às secretarias de Segurança dos Estados ou à Polícia Federal. Agora, os cartórios podem auxiliar o Poder Executivo a expedir os documentos, ampliando a rede de atendimento.</p>
        <p className="simple-text">O intuito do governo é diminuir a burocracia e facilitar a vida do cidadão. Para garantir a idoneidade do processo, os convênios locais passarão pelas corregedorias dos tribunais estaduais e os federais, pela Corregedoria Nacional. A Polícia Federal fica responsável pelos acordos relativos à emissão do passaporte e continua sendo o órgão responsável pela emissão do documento.</p>
        <p className="smaller-title-text">Fonte: Conselho Nacional de Justiça</p>
    </div>
)


