import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Governo oferece melhor tratamento do mundo para HIV/Aids</h2>
        <p className="simple-text">Ao incorporar o antirretroviral dolutegravir ao Sistema Único de Saúde (SUS), o Governo do Brasil passou a oferecer o melhor tratamento do mundo para HIV/Aids. Inicialmente ofertado a 100 mil pacientes, o tratamento é gradativamente ampliado a todos os portadores do vírus que são acompanhados pelo SUS até o fim de 2018.</p>
        <p className="eye-text">A oferta do tratamento ocorre após a redução negociada de 70% no valor do medicamento. Dessa forma, a incorporação pode ser feita sem alterar o orçamento da Saúde para a aquisição de antirretrovirais.</p>
        <p className="simple-text">Considerado um dos melhores medicamentos do mundo para pessoas que vivem com HIV e Aids, o dolutegravir apresenta uma série de vantagens como alta potência; nível muito baixo de eventos adversos; comodidade para o paciente com uma dose ao dia; e tratamento eficaz por mais tempo e menor resistência.</p>
        <p className="smaller-title-text">Fonte: Ministério da Saúde</p>
    </div>
)


