import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Integração do rio São Francisco já traz melhorias e gera milhares de empregos</h2>

        <p className="simple-text">Com a retomada da obra de integração do rio São Francisco, as águas começaram a fluir pelos canais concluídos em 33 cidades da Paraíba e Pernambuco em março do ano passado. Desde então, um milhão de residências passaram a ser abastecidas. Moradores usam o recurso para consumo, plantio e criação de animais, recebendo mais qualidade de vida e gerando recursos econômicos.</p>
        <p className="eye-text">Em 2018, já são 297 quilômetros de canais e adutoras em operação, sendo 217 quilômetros no Eixo Leste. Com a empreitada que segue, 5,6 mil moradores da região estão contratados na construção dos canais, melhorando a economia. </p>
        <p className="simple-text">A geração de empregos também aconteceu com a inauguração em março dos 42 km de canal do Eixo Norte do Projeto Público de Irrigação Baixio de Irecê, na Bahia. A irrigação das terras cria 25 mil empregos diretos e indiretos e favorece lavouras de algodão, cana-de-açúcar, frutas e legumes, beneficiando inúmeras famílias. </p>
        <p className="simple-text">Parte do programa Agora, é Avançar, a obra está 96,5% concluída. Quando estiver pronta, vai abastecer 23 açudes nordestinos, cujas capacidades estavam comprometidas pela seca prolongada. A expectativa é que até 12 milhões de pessoas sejam beneficiadas. O Governo do Brasil investiu R$ 550 milhões no projeto.</p>
        <p className="smaller-title-text">Fonte Planalto e Codevasf</p>
    </div>
)


