import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Intervenção federal contribui para redução de índices de criminalidade no Rio de Janeiro</h2>

        <p className="simple-text">Após a intervenção federal no Rio de Janeiro e a criação do Ministério Extraordinário da Segurança Pública em fevereiro, os índices de criminalidade na capital carioca já apontam melhoras. </p>
        <p className="eye-text">Medidas adotadas na cidade são de longo prazo, mas resultados positivos são constatados desde o primeiro mês.</p>
        <p className="simple-text">Em março, ocorrências como roubo a transeunte tiveram queda de 55% em relação ao mesmo período de 2017, passando de 559 para 251 casos. Crimes como roubo de carga e roubo a turista também seguiram a tendência de diminuição. De janeiro a março, o roubo de veículos caiu 18% no centro do estado do Rio de Janeiro e mais de 30% na capital e na Baixada Fluminense.</p>
        <p className="simple-text">Afim de custear as ações, medida provisória assinada pelo presidente da República, Michel Temer, em março garante orçamento extra de R$ 1,2 bilhão para as operações da intervenção federal na segurança pública do Rio de Janeiro.</p>
        <p className="smaller-title-text">Fonte: Planalto, Ministério da Defesa</p>
    </div>
)


