import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Polícia Federal apreende 44,7 toneladas de cocaína e 313 de maconha e bate recorde histórico</h2>

        <p className="simple-text">A Polícia Federal bateu seu recorde histórico de apreensões de cocaína e maconha em 2017. Com operações das Delegacias de Repressão a Entorpecentes e do Grupo Especial de Investigações Sensíveis (Gise), a PF tirou de circulação 44,7 toneladas de cocaína e 313 toneladas de maconha.</p>
        <p className="eye-text">O valor de bens apreendidos também é recorde: R$ 591,4 milhões. Antes, a maior cifra era de 2014, com R$ 323 milhões. A polícia rastreou, e a Justiça apreendeu mais de R$ 665 milhões em bens e em dinheiro do tráfico, quase o triplo do que em 2016.</p>
        <p className="simple-text">Nas rodovias federais, entre julho de 2017 e abril de 2018, foram 14 mil pessoas presas, 205 toneladas de maconha e seis toneladas de cocaína e crack apreendidas, além de 1 mil armas de fogo e 192 mil munições. Também foram recuperados 3.200 mil carros roubados/furtados/usados em outros crimes.</p>
        <p className="smaller-title-text">Fonte: Polícia Federal</p>
    </div>
)


