import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Novo formato da carteira de habilitação oferece mais segurança aos motoristas</h2>
        <p className="simple-text">A Carteira Nacional de Habilitação (CNH) terá um novo formato a partir de janeiro de 2019. O documento, atualmente em papel, será semelhante a um cartão de crédito, de plástico e com chip, para aumentar a segurança e evitar fraudes.</p>
        <p className="eye-text">Além da inovação no documento físico, os motoristas também podem contar com a versão eletrônica da CNH. O documento criptografado já está em vigor e fica acessível nos telefones celulares via um aplicativo seguro. A versão eletrônica possui a mesma validade do documento em papel.</p>
        <p className="simple-text">Além da resistência e da alta durabilidade, a nova CNH considera a possibilidade de inserção de dados e informações relativas aos condutores nos chips, bem como facilita acesso a certificados digitais. Ela amplia as possibilidades de uso dos documentos, a consulta e a verificação de inúmeros dados</p>
        <p className="smaller-title-text">Fonte: Ministério das Cidades</p>
    </div>
)


