import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Governo amplia participação de brasileiros no Mais Médico</h2>
        <p className="simple-text">Ampliar a participação de brasileiros no programa Mais Médicos é um compromisso do Governo do Brasil. Com a mudança de gestão, em um ano, foi possível aumentar o número de nacionais em 44%. Em 2018, já são 8.459 (46%) brasileiros formados no Brasil ou no exterior que participam da estratégia.</p>
        <p className="eye-text">Editais periódicos são lançados para repor e substituir médicos da cooperação com a Organização Pan-Americana de Saúde (Opas) por profissionais brasileiros, que têm respondido positivamente à iniciativa. No primeiro resultado anunciado em 2018, 99% das 983 vagas foram preenchidas por profissionais com registro no Brasil.</p>
        <p className="simple-text">Com sua continuidade assegurada, o Mais Médicos ampliou a assistência médica na atenção básica, provendo atendimento regular nas unidades básicas de saúde, na composição de novas equipes de saúde da família ou ainda em equipes que não contavam com profissionais no momento da adesão.</p>
        <p className="smaller-title-text">Fonte: Ministério da Saúde</p>
    </div>
)


