import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Com vacinação, Brasil recebe certificado internacional de livre da febre aftosa</h2>
        <p className="simple-text">Em 2018, a Organização Mundial de Saúde Animal (OIE) concede o título de reconhecimento do Brasil como país livre da febre aftosa com vacinação. A certificação é fruto de 50 anos de ação do governo em conjunto com a iniciativa privada, composta por mais de 2,7 milhões de fazendeiros.</p>
        <p className="eye-text">Dono do maior rebanho comercial do mundo, com 218,7 milhões de cabeças de bovinos e búfalos, o Brasil é também o maior exportador de carne, com vendas para mais de 140 países. Por isso, a erradicação da febre aftosa traz benefícios econômicos para o País. A certificação atesta a qualidade do produto e gera confiança no mercado interno e externo.</p>
        <p className="simple-text">O último caso da febre registrado no Brasil foi em 2006. Porém, o Programa Nacional de Erradicação e Prevenção da Febre Aftosa (Pnefa) continua vigente para os próximos 10 anos. O objetivo é criar e manter condições sustentáveis para garantir o status de país livre da doença e ampliar as zonas livres sem vacinação, protegendo o patrimônio pecuário nacional.</p>
        <p className="smaller-title-text">Fonte: Ministério da Agricultura</p>
    </div>
)


