import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Governo investe R$ 1 bilhão na formação de professores</h2>
        <p className="simple-text">Ao investir R$ 1 bilhão na Política Nacional de Formação de Professores em 2018, o Governo do Brasil reforça o compromisso com a formação profissional dos docentes brasileiros. Ao total, são 190 mil vagas criadas, sendo 45 mil no Programa Institucional de Bolsas de Iniciação à Docência (Pibid), 45 mil no Programa de Residência Pedagógica e mais 100 mil na Universidade Aberta do Brasil (UAB).</p>
        <p className="simple-text">A formação dos professores faz parte da política do governo de investimentos constantes no sistema educacional do Brasil. Ter docentes mais qualificados significa um ensino de mais qualidade para crianças e jovens e bases mais prósperas para a economia do País continuar crescendo.</p>
        <p className="smaller-title-text">Fonte: Ministério da Educação</p>
    </div>
)


