import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Governo propõe salário mínimo de R$ 1.002 para 2019</h2>
        <p className="simple-text">Pela primeira vez no Brasil depois do Plano Real, o valor do salário mínimo ultrapassará R$ 1 mil. Na proposta de Lei de Diretrizes Orçamentárias (LDO) enviada ao Congresso Nacional e assinada pelo presidente da República, Michel Temer, está a sugestão de R$ 1.002, aumento de 5% em relação ao valor atual – R$ 954. </p>
        <p className="eye-text">A proposta foi possível graças à política econômica implantada a partir de maio de 2016.</p>
        <p className="simple-text">O valor anunciado respeita a aplicação das regras atuais de correção, que define que o reajuste do salário mínimo deve ser corrigido pela inflação do ano anterior, medida pelo Índice Nacional de Preços ao Consumidor (INPC), somada à variação do Produto Interno Bruto (PIB, soma dos bens e dos serviços produzidos no País) dos dois anos anteriores.</p>
        <p className="smaller-title-text">Fonte: Ministério do Planejamento</p>
    </div>
)


