import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Repasses na Saúde ganham reforço à atenção básica com atualização da base populacional</h2>
        <p className="simple-text">A população brasileira será beneficiada com reforço anual de R$ 306 milhões no Piso da Atenção Básica, principal porta de entrada do Sistema Único de Saúde (SUS). Em 2018, o valor total do piso repassado será de R$ 5,1 bilhões. O adicional ocorre pela atualização da base populacional dos municípios, que serve de referência para o cálculo e que passa a ter como referência a estimativa de 2016 do IBGE. Até então, a orientação utilizada era a de 2012.</p>
        <p className="eye-text">Prioridade do governo, a atenção básica tem recebido recursos crescentes para melhorar a saúde da população que depende da rede pública. Com o adicional, será possível qualificar os serviços da especialidade, nos quais 80% dos problemas de saúde podem ser resolvidos,.</p>
        <p className="simple-text">Nos últimos dois anos, foram investidos mais de R$ 1 bilhão para custear e reforçar os serviços e equipes. O valor garante ampliação nos atendimentos e contempla mais de 20 milhões de pessoas que vivem em 2.386 municípios brasileiros.</p>
        <p className="smaller-title-text">Fonte: Ministério da Saude</p>
    </div>
)


