import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Governo lança documentos digitais que simplificam a vida do brasileiro</h2>
        <p className="simple-text">O Governo do Brasil dá passos importantes rumo à identificação digital do cidadão brasileiro e à redução da burocracia na administração. As inovações possibilitam ainda mais agilidade nos processos e mais transparência para a população.</p>
        <p className="simple-text">Uma das soluções propostas, o Documento Nacional de Identificação (DNI) reúne diferentes registros civis em um único documento, com validade em todo território nacional. A nova identificação dispensa a apresentação de outros papéis como CPF, certidão de nascimento, casamento ou título de eleitor.</p>
        <p className="eye-text">Pelo aplicativo, o empregado pode verificar informações pessoais, de contratos de trabalho e também solicitar a primeira e a segunda via da carteira de trabalho física.</p>
        <p className="simple-text">Também realidade, a Carteira de Trabalho Digital permite que os trabalhadores tenham, em mãos, sempre que precisarem, todas as informações que constam no documento físico. Apesar de não substitui-lo, o documento facilita as consultas.</p>
        <p className="smaller-title-text">Fonte: Ministério do Planejamento e Ministério do Trabalho</p>
    </div>
)


