import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Discutido há mais de 20 anos, novo Ensino Médio traz mais educação para os jovens </h2>

        <p className="simple-text">Depois de mais de 20 anos de debates, a reforma do ensino médio foi aprovada pelo Congresso Nacional e sancionada pelo presidente da República, Michel Temer, em fevereiro de 2017. As mudanças são mais um passo decisivo na modernização do País ao estabelecer novas regras, mais adequadas à sociedade brasileira atual.</p>
        <p className="eye-text">Com as mudanças, o modelo brasileiro para o ensino médio se aproxima daqueles adotados nos países que são referência de educação no mundo.</p>
        <p className="simple-text">O novo sistema de educação dos jovens faz da educação uma prioridade e cria condições para transformar a vida de milhões de brasileiros. Entre as modificações está a possibilidade de o currículo ficar mais flexível, permitindo que o aluno escolha a área de seu maior interesse para aprofundamento de estudos ou uma área de ensino técnico profissional.</p>
        <p className="simple-text">Destaque ainda para ampliação do ensino integral: foram 500 mil novas vagas de tempo integral em 2017. Todas voltadas para o jovem do ensino médio, beneficiando todos os estados e o Distrito Federal.</p>
        <p className="smaller-title-text">Fonte: Ministério da Educação</p>
    </div>
)


