import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Brasil amplia seu parque e ocupa 8° lugar no ranking de produção de energia eólica</h2>

        <p className="simple-text">A ampliação da capacidade instalada de produção de energia eólica do Brasil permitiu ao País se destacar frente aos grandes produtores internacionais. Em 2018, o Brasil, que já era o maior produtor na América Latina, se tornou o oitavo no ranking mundial, passando na frente do Canadá.</p>
        <p className="eye-text">Além de possuir ventos apropriados para a atividade, o País se destaca também pela média de produtividade, duas vezes superior à média mundial. A cadeia produtiva também é 80% nacionalizada, gerando investimentos e empregos no País.</p>
        <p className="simple-text">Hoje, o Brasil possui 518 parques eólicos e mais de 6.600 aerogeradores operando que geram 13 Gigawatts de energia elétrica. Esse montante equivale ao consumo médio de cerca de 24 milhões de residências por mês. Conhecidamente uma região de ventos vigorosos, o Nordeste é responsável por 60% da produção.</p>
        <p className="smaller-title-text">Fonte: ABE Eólica</p>
    </div>
)


