import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Brasil tem recorde de produção e se torna maior produtor de petróleo da América Latina</h2>

        <p className="simple-text">Com produção recorde em 2017, o Brasil se tornou o maior produtor de petróleo da América Latina, superando países como México e Venezuela. No ano, a produção média atingiu a marca inédita de 2,15 milhões de barris por dia. A produção própria de gás natural também registrou volume inédito de 79,6 milhões de metros cúbicos por dia.</p>
        <p className="eye-text">Parte importante desse resultado deve-se à nova gestão da Petrobras, que avança para recuperar a confiança internacional.</p>
        <p className="simple-text">Em fevereiro, a agência de classificação de risco Standard &amp; Poor’s (S&amp;P) mudou a classificação de risco da dívida corporativa da companhia de B+ para BB- e a perspectiva de negativa para estável. Isso, pois há evolução da sua liquidez e uma robusta posição de caixa que diminuiu os riscos de a companhia não honrar seus compromissos.</p>
        <p className="simple-text">Além dos investimentos feitos pela companhia, o incentivo à competição e alterações no marco regulatório do pré-sal, que desobriga a operadora a ser a única operadora, foram cruciais para o realinhamento da Petrobras.</p>
        <p className="smaller-title-text">Fontes: Petrobras e Ministério de Minas e Energia</p>
    </div>
)


